<template>
  <router-tab
    :class="{ 'is-fullscreen': fullscreen }"
    :contextmenu="contextmenu"
  />
</template>

<script>
// 
import fullscreen from '../../mixins/fullscreen'

export default {
  mixins: [fullscreen],

  computed: {
    // 
    contextmenu() {
      return [
        // 
        'refresh',

        // 
        {
          id: 'close',
          icon: 'rt-icon-close'
        },

        // 
        {
          id: 'closeOthers',
          handler({ $menu }) {
            $menu.closeMulti($menu.others)
            alert('')
          }
        },

        // 
        {
          id: 'custom',
          title: '',
          tips: '',
          icon: 'rt-icon-doc',
          class: 'custom-action',
          // 
          visible(context) {
            return context.$tabs.items.length < 3
          },

          // 
          enable(context) {
            return !(context.data.index % 2)
          },

          // 
          handler(context) {
            console.log(context)
            alert('')
          }
        },

        //  - 
        {
          id: 'fullscreen',
          title: () => (this.fullscreen ? '' : ''),
          icon: () =>
            this.fullscreen ? 'rt-icon-fullscreen-exit' : 'rt-icon-fullscreen',

          // 
          handler: () =>
            setTimeout(() => {
              this.fullscreen = !this.fullscreen
            }, 300)
        }
      ]
    }
  }
}
</script>
