import { importPage } from '../utils'

// 
export default () => [
  {
    path: 'page/:id',
    component: importPage('Page'),
    meta: {
      title: route => `${route.params.id}`,
      icon: 'rt-icon-doc',
      key: 'path'
    }
  },
  {
    path: 'no-cache',
    redirect: 'no-cache/1'
  },
  {
    path: 'no-cache/:id',
    component: importPage('Page'),
    meta: {
      title: route => `${route.params.id}`,
      keepAlive: false,
      icon: 'rt-icon-doc',
      key: 'path'
    }
  },
  {
    path: 'rule',
    redirect: 'rule/default/a/1'
  },
  {
    path: 'rule/default',
    redirect: 'rule/default/a/1'
  },
  {
    path: 'rule/default/:catalog/:type',
    component: importPage('Rule'),
    meta: {
      title: route => `:-${route.params.catalog}/${route.params.type}`,
      icon: 'rt-icon-log'
    }
  },
  {
    path: 'rule/path',
    redirect: 'rule/path/a/1'
  },
  {
    path: 'rule/path/:catalog/:type',
    component: importPage('Rule'),
    meta: {
      title: route => `:path-${route.params.catalog}/${route.params.type}`,
      icon: 'rt-icon-log',
      key: 'path'
    }
  },
  {
    path: 'rule/fullPath',
    redirect: 'rule/fullPath/a/1'
  },
  {
    path: 'rule/fullPath/:catalog/:type',
    component: importPage('Rule'),
    meta: {
      title: route =>
        `:fullPath-${route.params.catalog}/${route.params.type}`,
      icon: 'rt-icon-log',
      key: 'fullPath'
    }
  },
  {
    path: 'rule/custom',
    redirect: 'rule/custom/a/1'
  },
  {
    path: 'rule/custom/:catalog/:type',
    component: importPage('Rule'),
    meta: {
      title: route =>
        `:-${route.params.catalog}/${route.params.type}`,
      icon: 'rt-icon-log',
      key: route => '/rule/custom/' + route.params.catalog
    }
  },
  {
    path: 'tab-dynamic',
    component: importPage('TabDynamic'),
    meta: {
      title: '',
      icon: 'rt-icon-log'
    }
  },
  {
    path: 'page-leave',
    component: importPage('PageLeave'),
    meta: {
      title: '',
      icon: 'rt-icon-contact'
    }
  },
  {
    path: 'nest/:nestId',
    component: importPage('Nest'),
    redirect(route) {
      return route.fullPath + '/page1'
    },
    meta: {
      title: '',
      icon: 'rt-icon-doc'
    },
    children: [
      {
        path: 'page1',
        component: importPage('Page1')
      },
      {
        path: 'page2',
        component: importPage('Page2')
      }
    ]
  }
]
